.image-gallery-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 95%;
  max-width: 800px;
  max-height: 85vh;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 24px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.2);
  text-align: center;
  z-index: 100;
  padding: 30px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.image-gallery-container.pulled {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
}

.image-gallery-container h2 {
  color: #ff4d6d;
  font-family: 'Dancing Script', cursive;
  font-size: 3rem;
  margin-bottom: 25px;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 20px;
  padding: 10px;
}

.gallery-item {
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  aspect-ratio: 1 / 1;
}

.gallery-item:hover {
  transform: scale(1.05);
}

.gallery-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.image-gallery-container p {
  color: #4a0e0e;
  font-family: 'Poppins', sans-serif;
}

/* Custom Scrollbar */
.image-gallery-container::-webkit-scrollbar {
  width: 8px;
}

.image-gallery-container::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

.image-gallery-container::-webkit-scrollbar-thumb {
  background: #ff4d6d;
  border-radius: 10px;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .image-gallery-container {
    padding: 20px 15px;
  }
  
  .image-gallery-container h2 {
    font-size: 2.2rem;
  }
  
  .gallery-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
}
